import React, { Component } from 'react';
import './reset.css';
import { CSSTransitionGroup } from 'react-transition-group'; // ES6
import './App.css';


class Contact extends Component {
  render() {
    return (
      <div className="contact-content">
            <div className="ab-text">
              <CSSTransitionGroup
                transitionName={"contact"}
                transitionAppear={true}
                transitionAppearTimeout={900}
                transitionEnterTimeout={false}
                transitionLeaveTimeout={false}
                >
                  <h1 style={{fontSize:'15em', color:'#444', fontWeight:'500'}}>C</h1>
                  <h2>GET IN TOUCH! </h2>
                  <h4>rcapde@gmail.com</h4><br /><br />
                  <h5>More about me on...</h5><br />
                  <a href="https://www.linkedin.com/in/rcapde7/" target="_blank"><p style={{fontSize:'0.8em', fontWeight:'300'}}>LINKEDIN</p></a>
                  <a href="https://github.com/rcapde" target="_blank"><p style={{fontSize:'0.8em', fontWeight:'300'}}>GITHUB</p></a>
                </CSSTransitionGroup>

            </div>

      </div>

    );
  }
}

export default Contact;



