import React, { Component } from 'react';
import Navbarlt from './components/navbarlt';

import './reset.css';
import './App.css';
import { CSSTransitionGroup } from 'react-transition-group'; // ES6


class Home extends Component {
  render() {
    return (
      <div className="as">
      <CSSTransitionGroup
        transitionName={"home"}
        transitionAppear={true}
        transitionAppearTimeout={300}
        transitionEnterTimeout={false}
        transitionLeaveTimeout={false}
        >
        <div className="middle">
          <div className="header">
          <h1>A
          <q className="qoriginal">REACTIVE</q>
          <q className="qwork">WORK</q>
          </h1>
          </div>

            <div className="subheader"><h2>FRONT END DEVELOPMENT</h2></div>
          </div>

          </CSSTransitionGroup>

      </div>

    );
  }
}

export default Home;



