import React, { Component } from 'react';
import WorkItem from './WorkItem';

import './reset.css';
import './App.css';
import ScrollAnimation from 'react-animate-on-scroll';


class Works extends Component {

  render() {
    return (
      <div className="work-content">
      <ScrollAnimation animateIn="fadeIn" animateOut="fadeOut">
        <div className="workitem">
          <h2 className="workitem-h2">WebApp and App projects</h2>


        </div>
        <div className="arrowBot"><img className="workitem-img" src="./botarrow.png" width="80px" /></div>

      </ScrollAnimation>
        <ScrollAnimation animateIn="fadeIn" animateOut="fadeOut">
          <div className="workitem">
            <p className="workitem-p">WebApp/dev and desing</p>
            <h2 className="workitem-h2">Click n Copy color picker</h2>
            <a href="http://www.uicolorspot.com" target="_blank"><img className="workitem-img" src="./projects/uicolors.png" width="600px" /></a>
          </div>
        </ScrollAnimation>

        <ScrollAnimation animateIn='bounceInRight' duration={3}
          animateOut='bounceOutLeft'>
            <div className="workitem">
              <p className="workitem-p">WebApp dev and desing</p>
              <h2 className="workitem-h2">Free PhotoStock Website</h2>
              <a target="_blank" href="http://freecover.surge.sh">
                <img className="workitem-img" src="./projects/freecover.png" width="600px" />
              </a>
            </div>
          </ScrollAnimation>

          <ScrollAnimation animateIn='bounceInLeft' duration={3}
            animateOut='bounceOutRight'>
            <div className="workitem">
              <p className="workitem-p">VR design and development</p>
              <h2 className="workitem-h2">VR Project with Reactjs</h2>
              <a target="_blank" href="http://snowbox.surge.sh">
                <img className="workitem-img" src="./projects/snowbox.png" width="600px" />
              </a>
            </div>
          </ScrollAnimation>
          <ScrollAnimation animateIn="fadeIn" animateOut="fadeOut">
            <div className="workitem">
              <p className="workitem-p">App development</p>
              <h2 className="workitem-h2">Hybrid App development with React Native</h2>
                <div className="workitem-gallery">
                  <img className="workitem-img" src="./projects/eco1.png" width="200px" />
                  <img className="workitem-img" src="./projects/eco2.png" width="200px" />
                  <img className="workitem-img" src="./projects/eco3.png" width="200px" />
              </div>
            </div>
          </ScrollAnimation>



      </div>


    );
  }

}

export default Works;



